body {
  box-sizing: border-box;
  margin: 0;
  padding: 0 0;
}

header {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10vh;
  background: black;
  color: white;

  font-family: Garamond Georgia Arial;
  font-size: 40px;
  opacity: 0.95;
  text-align: center;
  transition: all 0.5s;
  /* box-sizing: border-box; */
}

header * {
  opacity: 0.95;
  color: white;
  font-family: Garamond Georgia Arial;
  font-size: 40px;
  border: none;
}

header:hover {
  color: black;
  background: white;
}

#content {
  height: 70vh;
  background: pink;
}

#photo {
  height: 100%;
  margin: 0 auto;
  width: 80vw;
  background: black;
}

img {
  max-width: 100%;
  max-height: 100%;
  width: 100%;
  object-fit: contain;
}

.arrow {
  position: absolute;
  background: black;
  top: 35vh;
  height: 10vh;
  width: 10vh;
  border-radius: 5vh;
}

.arrow.left {
  position: absolute;
  left: 20px;
}

.arrow.left .triangle {
  position: relative;
  right: 2.7vh;
  top: 1vh;
  height: 0;
  width: 0;
  border: 4vh solid transparent;
  border-right: 6vh solid white;
}

.arrow.right {
  position: absolute;
  right: 20px;
}

.arrow.right .triangle {
  position: relative;
  left: 2.8vh;
  top: 1vh;
  height: 0;
  width: 0;
  border: 4vh solid transparent;
  border-left: 6vh solid white;
}

/* input[name="q"] {
  background: grey;
  color: white;
} */

input[type="submit"] {
  background: #ff8d2c;
  color: white;
  padding: 10px;
  width: 10vw;
  height: 30%;
  border-radius: 5px;
}

.gallery-preview {
  height: 20vh;
  background: black;
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
}

#debug {
  height: 20vh;
  background: pink;
  color: black;
}

#overlay {
  position: fixed;
  display: none;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.6);
  left: 0;
  top: 0;
}

#enable-cors {
  display: block;
  position: absolute;
  left: 0;
  background: transparent;
  font-size: 20px;
}

#enable-cors:hover {
  color: black;
  background: white;
  transition: all 0.5s;
}

/*************************** MODAL ************************/
.modal {
  position: absolute;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
  background-color: white;
  width: 30%;
  height: 30%;
}

.cors-link-modal {
  position: absolute;
  left: 50%;
  top: 55%;
  transform: translate(-50%, -50%);
  width: 70%;
}

.close-modal {
  position: absolute;
  padding: 20px;
  top: 0;
  right: 0;
}
